import SudokuSolver from "./SudokuSolver";
export default class SudokuFactory {
    board!: number[];
    blocks!: number[][];
    constructor() {
    }
    generate(spaceNum = 1) {
        this.board = new Array(81).fill(0);
        this.blocks = [];
        for (let i = 0; i < 9; i++) {
            let curBlock = [];
            let rowStart = Math.floor(i / 3) * 3;
            let colStart = Math.floor(i % 3) * 3;
            for (let j = rowStart; j < rowStart + 3; j++) {
                for (let k = colStart; k < colStart + 3; k++) {
                    curBlock.push(j * 9 + k);
                }
            }
            this.blocks.push(curBlock)
        }
        this.randBlock(this.blocks[0]);
        this.randBlock(this.blocks[4]);
        this.randBlock(this.blocks[8]);

        let sudokuSolver = new SudokuSolver();
        sudokuSolver.setData(this.getBoardData());
        let result = sudokuSolver.solve(1)[0];
        this.placeSpace(result, spaceNum);
        return result;
    }
    randBlock(block: number[]) {
        let nums = [1, 2, 3, 4, 5, 6, 7, 8, 9];
        nums.sort(() => {
            return Math.random() - 0.5;
        });
        for (let i = 0; i < 9; i++) {
            this.board[block[i]] = nums[i];
        }
    }
    getBoardData() {
        let data: number[][] = [];
        for (let i = 0; i < 9; i++) {
            data.push([])
            for (let j = 0; j < 9; j++) {
                data[i][j] = this.board[i * 9 + j];
            }
        }
        return data;
    }
    placeSpace(result: number[][], spaceNum: number) {
        let arr = []
        for (let i = 0; i < 81; i++) {
            arr.push(i);
        }
        for (let i = 0; i < spaceNum; i++) {
            let rand = Math.floor(Math.random() * arr.length);
            let randNum = arr[rand];
            let row = Math.floor(randNum / 9);
            let col = randNum % 9;
            result[row][col] = 0;
            arr.splice(rand, 1);
        }

    }
}