











import { Component, Prop, Vue, Emit } from "vue-property-decorator";
@Component
export default class BoardCheck extends Vue {
  @Prop(Number)
  curRow!: Number;
  @Prop(Number)
  curCol!: Number;
  @Prop(Boolean)
  checkShow!: Boolean;
  @Emit("inputnum")
  inputNum(num:number) {
      return num;
  }
}
