


















import { Component, Prop, Vue, Emit } from "vue-property-decorator";
import Square from "./Square.vue";
import BoardCheck from "./BoardCheck.vue";
class Pos {
  x: number;
  y: number;
  constructor() {
    this.x = -1;
    this.y = -1;
  }
}
@Component({
  components: {
    BoardCheck,
    Square
  }
})
export default class Board extends Vue {
  @Prop()
  items!: number[][];
  @Prop()
  isBlank!: boolean[][];
  curRow: number = -1;
  curCol: number = -1;
  hoverCol: number = -1;
  checkShow: boolean = false;
  errInput: Pos = new Pos();
  errRow: Pos = new Pos();
  errCol: Pos = new Pos();
  isShake: boolean = false;
  isError: boolean = false;
  showCheck(row: number, col: number) {
    if (this.isError) {
      return;
    }
    if (this.isBlank[row][col]) {
      if (row === this.curRow && col === this.curCol) {
        this.checkShow = false;
      } else {
        this.curRow = row;
        this.curCol = col;
        this.checkShow = true;
      }
    }
  }
  showHoverCol(value: number) {
    console.log("showHoverCol", value);
    this.hoverCol = value;
  }
  @Emit()
  inputNum(value: number) {
    let info = [this.curRow, this.curCol, value];
    this.checkError(this.curRow, this.curCol, value);
    this.curRow = -1;
    this.curCol = -1;
    this.checkShow = false;
    return info;
  }
  @Emit()
  deleteNum(row: number, col: number, value: number) {
    return [row, col, value];
  }
  checkError(row: number, col: number, value: number) {
    let isError = false;
    this.errRow = new Pos();
    this.errInput = new Pos();
    this.errCol = new Pos();
    this.items[row].forEach((s, index) => {
      if (s === value) {
        this.errRow.x = row;
        this.errRow.y = index;
        isError = true;
      }
    });
    for (let i = 0; i < this.items.length; i++) {
      for (let j = 0; j < this.items[i].length; j++) {
        if (j === col && this.items[i][j] === value) {
          this.errCol.x = i;
          this.errCol.y = col;
          isError = true;
        }
      }
    }
    if (isError) {
      this.isError = true;
      this.isShake = true;
      this.errInput.x = row;
      this.errInput.y = col;
      setTimeout(() => {
        this.isError = false;
        this.isShake = false;
        this.errInput = new Pos();
        this.errRow = new Pos();
        this.errCol = new Pos();
        this.deleteNum(row, col, value);
      }, 1000);
    }
  }
}
