














import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Board from "./components/Board.vue";
import SudokuFactory from "./util/SudokuFactory";
import SudokuSolver from "./util/SudokuSolver";
@Component({ components: { Board } })
export default class App extends Vue {
  items: number[][];
  isBlank: boolean[][];
  sudokuFactory: SudokuFactory;
  sudokuSolver: SudokuSolver;
  constructor() {
    super();
    this.items = [];
    this.isBlank = [];
    this.sudokuFactory = new SudokuFactory();
    this.sudokuSolver = new SudokuSolver();
  }
  mounted() {
    this.$nextTick(() => {
      this.showSubject(1);
    });
  }
  showSubject(degree: number) {
    let blankNum = Math.max(1, degree * 9 - 1);
    this.items = this.sudokuFactory.generate(blankNum);
    let isBlank: boolean[][] = [];
    for (let i = 0; i < 9; i++) {
      isBlank.push([]);
      for (let j = 0; j < 9; j++) {
        if (this.items[i][j] === 0) {
          isBlank[i][j] = true;
        } else {
          isBlank[i][j] = false;
        }
      }
    }
    this.isBlank = isBlank;
  }
  solve() {
    this.sudokuSolver.setData(this.items);
    let answers = this.sudokuSolver.solve(1);
    this.items = answers[0];
  }
  clearSudoku() {
    for (let i = 0; i < this.items.length; i++) {
      let row = new Array(9).fill(0);
      this.items.splice(i, 1, row);
    }
  }
  inputNum(arg: number[]) {
    let row = arg[0];
    let col = arg[1];
    let value = arg[2];
    this.items[row].splice(col, 1, value);
    let gameOver = this.items.every(row => {
      return row.every(num => num > 0);
    });
    if (gameOver) {
      alert("你很棒，已经全部解出来了！");
    }
  }
  deleteNum(arg: number[]) {
    let row = arg[0];
    let col = arg[1];
    this.items[row].splice(col, 1, 0);
  }
}
